"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Progress } from "@/components/ui/progress";
import { useToast } from "@/components/ui/use-toast";
import {
  ArrowLeft,
  BookOpen,
  Calendar,
  Clock,
  FileText,
  User,
  UserCheck,
  MessageCircle,
  Bookmark,
  ChevronRight,
  AlertCircle,
  FileIcon,
  ExternalLink,
  School,
  Bell,
  Trophy,
  CheckCircle,
  XCircle,
  Loader2,
} from "lucide-react";
import Link from "next/link";
import { format, formatDistanceToNow } from "date-fns";
import { ClassDetailsType, QuizType } from "../../types/types";

interface ClassDetailContentProps {
  classId: string;
}

export function ClassDetailContent({ classId }: ClassDetailContentProps) {
  const router = useRouter();
  const { toast } = useToast();
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [classData, setClassData] = useState<ClassDetailsType | null>(null);

  useEffect(() => {
    const fetchClassDetails = async () => {
      try {
        setIsLoading(true);
        setError(null);

        const response = await fetch(`/api/classes/${classId}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        });

        if (!response.ok) {
          throw new Error("Failed to fetch class details");
        }

        const data = await response.json();
        
        if (data.success) {
          setClassData(data.data);
        } else {
          throw new Error(data.message || "Failed to fetch class details");
        }
      } catch (err) {
        console.error("Error fetching class details:", err);
        setError((err as Error).message);
        toast({
          title: "Error",
          description: (err as Error).message,
          variant: "destructive",
        });
      } finally {
        setIsLoading(false);
      }
    };

    fetchClassDetails();
  }, [classId, toast]);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
      </div>
    );
  }

  if (error || !classData) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] text-center">
        <AlertCircle className="h-12 w-12 text-destructive mb-4" />
        <h3 className="text-lg font-semibold mb-2">Error Loading Class</h3>
        <p className="text-muted-foreground mb-4">{error || "Failed to load class details"}</p>
        <Button onClick={() => router.refresh()}>Try Again</Button>
      </div>
    );
  }

  // Format date
  const formatDate = (dateString: string) => {
    return format(new Date(dateString), "PPP");
  };

  // Calculate quiz statistics
  const quizStats = {
    total: classData.quizzes.length,
    upcoming: classData.quizzes.filter(q => q.status === "upcoming").length,
    ongoing: classData.quizzes.filter(q => q.status === "ongoing").length,
    completed: classData.quizzes.filter(q => q.status === "completed").length,
  };

  return (
    <div className="space-y-6">
      {/* Back button */}
      <Button variant="ghost" size="sm" asChild>
        <Link href="/dashboard/student/classes">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Classes
        </Link>
      </Button>

      {/* Class header with cover image */}
      <div className="relative rounded-lg overflow-hidden">
        <div 
          className={`h-48 w-full ${classData.coverImage ? 'bg-cover bg-center' : 'bg-gradient-to-r from-slate-100 to-slate-200'}`}
          style={classData.coverImage ? { backgroundImage: `url(${classData.coverImage})` } : {}}
        >
          <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
        </div>
        
        <div className="absolute bottom-0 left-0 p-6 text-white">
          <h1 className="text-2xl sm:text-3xl font-bold mb-2">{classData.name}</h1>
          <div className="flex flex-wrap items-center gap-x-4 gap-y-2">
            <div className="flex items-center">
              <User className="h-4 w-4 mr-2" />
              <span>{classData.teacherName}</span>
            </div>
            <div className="flex items-center">
              <BookOpen className="h-4 w-4 mr-2" />
              <span>{classData.subject}</span>
            </div>
            <div className="flex items-center">
              <UserCheck className="h-4 w-4 mr-2" />
              <span>{classData.totalStudents} students</span>
            </div>
          </div>
        </div>
      </div>

      {/* Main content tabs */}
      <Tabs defaultValue="overview" className="space-y-6">
        <TabsList>
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="quizzes">Quizzes</TabsTrigger>
          <TabsTrigger value="materials">Materials</TabsTrigger>
          <TabsTrigger value="announcements">Announcements</TabsTrigger>
        </TabsList>

        {/* Overview tab */}
        <TabsContent value="overview" className="space-y-6">
          <div className="grid gap-6 md:grid-cols-2">
            {/* Class information */}
            <Card>
              <CardHeader>
                <CardTitle>Class Information</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <h4 className="font-medium">Description</h4>
                  <p className="text-muted-foreground">{classData.description || "No description provided"}</p>
                </div>
                <Separator />
                <div className="space-y-2">
                  <div className="flex items-center justify-between">
                    <span className="text-muted-foreground">Teacher</span>
                    <span>{classData.teacherName}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-muted-foreground">Subject</span>
                    <span>{classData.subject}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-muted-foreground">Total Students</span>
                    <span>{classData.totalStudents}</span>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-muted-foreground">Joined Date</span>
                    <span>{formatDate(classData.joinedDate)}</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Quiz Statistics */}
            <Card>
              <CardHeader>
                <CardTitle>Quiz Statistics</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-1">
                    <p className="text-2xl font-bold">{quizStats.total}</p>
                    <p className="text-sm text-muted-foreground">Total Quizzes</p>
                  </div>
                  <div className="space-y-1">
                    <p className="text-2xl font-bold text-yellow-600">{quizStats.upcoming}</p>
                    <p className="text-sm text-muted-foreground">Upcoming</p>
                  </div>
                  <div className="space-y-1">
                    <p className="text-2xl font-bold text-blue-600">{quizStats.ongoing}</p>
                    <p className="text-sm text-muted-foreground">Ongoing</p>
                  </div>
                  <div className="space-y-1">
                    <p className="text-2xl font-bold text-green-600">{quizStats.completed}</p>
                    <p className="text-sm text-muted-foreground">Completed</p>
                  </div>
                </div>
                <Separator />
                <div className="space-y-2">
                  <h4 className="font-medium">Recent Activity</h4>
                  {classData.quizzes.slice(0, 3).map((quiz) => (
                    <div key={quiz.id} className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        {quiz.status === "upcoming" && <Clock className="h-4 w-4 text-yellow-600" />}
                        {quiz.status === "ongoing" && <BookOpen className="h-4 w-4 text-blue-600" />}
                        {quiz.status === "completed" && <CheckCircle className="h-4 w-4 text-green-600" />}
                        <span className="line-clamp-1">{quiz.title}</span>
                      </div>
                      <Badge variant={
                        quiz.status === "upcoming" ? "outline" :
                        quiz.status === "ongoing" ? "default" :
                        "secondary"
                      }>
                        {quiz.status}
                      </Badge>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        {/* Quizzes tab */}
        <TabsContent value="quizzes" className="space-y-6">
          <div className="grid gap-4">
            {classData.quizzes.length > 0 ? (
              classData.quizzes.map((quiz) => (
                <Card key={quiz.id}>
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <div>
                        <CardTitle className="flex items-center gap-2">
                          {quiz.title}
                          {quiz.isFormal && (
                            <Badge variant="outline" className="border-primary/40 text-primary">
                              Formal
                            </Badge>
                          )}
                        </CardTitle>
                        <CardDescription>
                          Duration: {quiz.durationMinutes} minutes â€¢ {quiz.totalQuestions} questions
                        </CardDescription>
                      </div>
                      <Badge variant={
                        quiz.status === "upcoming" ? "outline" :
                        quiz.status === "ongoing" ? "default" :
                        "secondary"
                      }>
                        {quiz.status}
                      </Badge>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-2">
                      <p className="text-sm text-muted-foreground line-clamp-2">
                        {quiz.description || "No description provided"}
                      </p>
                      <div className="flex flex-wrap gap-4 text-sm">
                        <div className="flex items-center">
                          <Calendar className="h-4 w-4 mr-2 text-muted-foreground" />
                          <span>
                            {quiz.startDate ? formatDate(quiz.startDate) : "No start date"}
                          </span>
                        </div>
                        {quiz.attemptLimit && (
                          <div className="flex items-center">
                            <Bookmark className="h-4 w-4 mr-2 text-muted-foreground" />
                            <span>
                              {quiz.currentAttempts || 0}/{quiz.attemptLimit} attempts
                            </span>
                          </div>
                        )}
                        {quiz.highestScore !== null && (
                          <div className="flex items-center">
                            <Trophy className="h-4 w-4 mr-2 text-muted-foreground" />
                            <span>Highest Score: {quiz.highestScore}%</span>
                          </div>
                        )}
                      </div>
                    </div>
                  </CardContent>
                  <CardFooter>
                    <Button 
                      className="w-full" 
                      variant={quiz.status === "completed" ? "secondary" : "default"}
                      disabled={quiz.isLocked}
                      asChild
                    >
                      <Link href={`/dashboard/student/quizzes/${quiz.id}`}>
                        {quiz.status === "completed" ? "View Results" : "View Quiz"}
                        <ChevronRight className="h-4 w-4 ml-2" />
                      </Link>
                    </Button>
                  </CardFooter>
                </Card>
              ))
            ) : (
              <div className="text-center py-12">
                <BookOpen className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                <h3 className="text-lg font-medium mb-2">No Quizzes Yet</h3>
                <p className="text-muted-foreground">
                  There are no quizzes available for this class yet.
                </p>
              </div>
            )}
          </div>
        </TabsContent>

        {/* Materials tab */}
        <TabsContent value="materials" className="space-y-6">
          <div className="grid gap-4">
            {classData.materials.length > 0 ? (
              classData.materials.map((material) => (
                <Card key={material.id}>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      {material.type === "document" && <FileText className="h-4 w-4" />}
                      {material.type === "video" && <FileIcon className="h-4 w-4" />}
                      {material.type === "link" && <ExternalLink className="h-4 w-4" />}
                      {material.title}
                    </CardTitle>
                    {material.description && (
                      <CardDescription>{material.description}</CardDescription>
                    )}
                  </CardHeader>
                  <CardContent>
                    <div className="text-sm text-muted-foreground">
                      Added {formatDistanceToNow(new Date(material.createdAt), { addSuffix: true })}
                    </div>
                  </CardContent>
                  <CardFooter>
                    <Button className="w-full" asChild>
                      <a href={material.url} target="_blank" rel="noopener noreferrer">
                        Open Material
                        <ExternalLink className="h-4 w-4 ml-2" />
                      </a>
                    </Button>
                  </CardFooter>
                </Card>
              ))
            ) : (
              <div className="text-center py-12">
                <FileText className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                <h3 className="text-lg font-medium mb-2">No Materials Yet</h3>
                <p className="text-muted-foreground">
                  There are no learning materials available for this class yet.
                </p>
              </div>
            )}
          </div>
        </TabsContent>

        {/* Announcements tab */}
        <TabsContent value="announcements" className="space-y-6">
          <div className="grid gap-4">
            {classData.announcements.length > 0 ? (
              classData.announcements.map((announcement) => (
                <Card key={announcement.id}>
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <div>
                        <CardTitle className="flex items-center gap-2">
                          {announcement.title}
                          {announcement.isNew && (
                            <Badge>New</Badge>
                          )}
                        </CardTitle>
                        <CardDescription>
                          Posted {formatDistanceToNow(new Date(announcement.createdAt), { addSuffix: true })}
                        </CardDescription>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <p className="whitespace-pre-wrap">{announcement.content}</p>
                  </CardContent>
                </Card>
              ))
            ) : (
              <div className="text-center py-12">
                <Bell className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                <h3 className="text-lg font-medium mb-2">No Announcements Yet</h3>
                <p className="text-muted-foreground">
                  There are no announcements for this class yet.
                </p>
              </div>
            )}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
} 